/**
 * Northwoods Embed SDK
 * Web components for embedding Northwoods widgets
 */

export { NorthwoodsWidget } from "./shared/base-widget";
export { ApiClient } from "./shared/api-client";
export { PledgeWidget } from "./components/pledge";

// Auto-register components
import "./components/pledge";

/**
 * Initialize the SDK with token provider
 */
export function init(config: {
  tokenProvider: {
    get: () => Promise<string>;
    refresh?: () => Promise<string>;
  };
}): void {
  window.__nwTokenProvider = config.tokenProvider;
}

// Export global types
declare global {
  interface Window {
    __nwTokenProvider?: {
      get: () => Promise<string>;
      refresh?: () => Promise<string>;
    };
  }
}
