import { NorthwoodsWidget } from "../shared/base-widget";

interface PledgeState {
  courageousGift: string;
  consistentGift: string;
  creativeGift: string;
  total: string;
  errors: Record<string, boolean>;
  isSubmitting: boolean;
  isSuccess: boolean;
  errorMessage: string;
}

interface FormData {
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
  address: string;
  city: string;
  state: string;
  zipcode: string;
  notes?: string;
}

export class PledgeWidget extends NorthwoodsWidget {
  private state: PledgeState = {
    courageousGift: "",
    consistentGift: "",
    creativeGift: "",
    total: "0.00",
    errors: {},
    isSubmitting: false,
    isSuccess: false,
    errorMessage: "",
  };

  private campaignId: number;

  constructor() {
    super();
    this.campaignId = parseInt(this.getAttribute("campaign-id") || "115");
  }

  connectedCallback() {
    this.injectStyles(this.getStyles());
    this.render();
    this.attachEventListeners();
  }

  private attachEventListeners() {
    this.root.addEventListener("input", (e) => this.handleInput(e));
    this.root.addEventListener("submit", (e) => this.handleSubmit(e));
  }

  private handleInput(e: Event) {
    const target = e.target as HTMLInputElement;

    if (
      target.name === "courageous_gift" ||
      target.name === "consistent_gift" ||
      target.name === "creative_gift"
    ) {
      const formatted = this.formatCurrency(target.value);
      target.value = formatted;

      if (target.name === "courageous_gift") this.state.courageousGift = formatted;
      if (target.name === "consistent_gift") this.state.consistentGift = formatted;
      if (target.name === "creative_gift") this.state.creativeGift = formatted;

      this.updateTotal();
    }

    if (target.name === "phone") {
      const formatted = this.formatPhoneNumber(target.value);
      target.value = formatted;
    }
  }

  private formatCurrency(value: string): string {
    const numericValue = value.replace(/[^0-9.]/g, "");
    const parts = numericValue.split(".");
    if (parts.length > 2) return value;

    const decimalPart = parts[1];
    if (decimalPart !== undefined && decimalPart.length > 2) return value;

    return numericValue;
  }

  private parseNumeric(value: string): number {
    const numeric = value.replace(/[^0-9.]/g, "");
    return parseFloat(numeric) || 0;
  }

  private formatPhoneNumber(value: string): string {
    const numeric = value.replace(/\D/g, "");
    const limited = numeric.substring(0, 10);

    if (limited.length <= 3) {
      return limited;
    } else if (limited.length <= 6) {
      return `${limited.slice(0, 3)}-${limited.slice(3)}`;
    } else {
      return `${limited.slice(0, 3)}-${limited.slice(3, 6)}-${limited.slice(6)}`;
    }
  }

  private updateErrorStates() {
    // Update error classes on inputs without re-rendering the whole form
    const inputs = this.root.querySelectorAll<HTMLInputElement | HTMLTextAreaElement>('input, select, textarea');
    
    inputs.forEach(input => {
      const fieldName = input.name;
      if (this.state.errors[fieldName]) {
        input.classList.add('error');
      } else {
        input.classList.remove('error');
      }
    });
  }

  private updateTotal() {
    const a = this.parseNumeric(this.state.courageousGift);
    const b = this.parseNumeric(this.state.consistentGift);
    const c = this.parseNumeric(this.state.creativeGift);
    const sum = a + b + c;
    this.state.total = sum.toFixed(2);
    
    const totalInput = this.root.querySelector<HTMLInputElement>('input[name="total_gift"]');
    if (totalInput) totalInput.value = this.state.total;
  }

  private validateEmail(email: string): boolean {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  private validatePhone(phone: string): boolean {
    const numericPhone = phone.replace(/\D/g, "");
    return numericPhone.length === 10;
  }

  private async handleSubmit(e: Event) {
    e.preventDefault();
    const form = e.target as HTMLFormElement;
    const formData = new FormData(form);

    const data: FormData = {
      firstName: formData.get("firstName") as string,
      lastName: formData.get("lastName") as string,
      email: formData.get("email") as string,
      phone: formData.get("phone") as string,
      address: formData.get("address") as string,
      city: formData.get("city") as string,
      state: formData.get("state") as string,
      zipcode: formData.get("zipcode") as string,
      notes: formData.get("notes") as string || undefined,
    };

    const newErrors: Record<string, boolean> = {};

    if (!data.firstName?.trim()) newErrors.firstName = true;
    if (!data.lastName?.trim()) newErrors.lastName = true;
    if (!data.email?.trim() || !this.validateEmail(data.email)) newErrors.email = true;
    if (!data.phone?.trim() || !this.validatePhone(data.phone)) newErrors.phone = true;
    if (!data.address?.trim()) newErrors.address = true;
    if (!data.city?.trim()) newErrors.city = true;
    if (!data.zipcode?.trim() || data.zipcode.length !== 5) newErrors.zipcode = true;

    const a = this.parseNumeric(this.state.courageousGift);
    const b = this.parseNumeric(this.state.consistentGift);
    const c = this.parseNumeric(this.state.creativeGift);

    if (a === 0 && b === 0 && c === 0) {
      newErrors.courageous_gift = true;
      newErrors.consistent_gift = true;
      newErrors.creative_gift = true;
    }

    this.state.errors = newErrors;

    if (Object.keys(newErrors).length > 0) {
      // Update error states without re-rendering entire form
      this.updateErrorStates();
      return;
    }

    this.state.isSubmitting = true;
    this.state.errorMessage = "";
    this.render();

    try {
      const idempotencyKey = crypto.randomUUID();

      const apiUrl = this.apiHost || "http://localhost:3000";
      const response = await this.fetch(`/api/embed/pledge/submit`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Idempotency-Key": idempotencyKey,
        },
        body: JSON.stringify({
          campaignId: this.campaignId,
          firstName: data.firstName,
          lastName: data.lastName,
          email: data.email,
          phone: data.phone,
          address: data.address,
          city: data.city,
          state: data.state,
          zipcode: data.zipcode,
          courageous_gift: this.state.courageousGift,
          consistent_gift: this.state.consistentGift,
          creative_gift: this.state.creativeGift,
          total_gift: this.state.total,
          notes: data.notes,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({ error: "Unknown error" }));
        throw new Error(errorData.error || `HTTP ${response.status}: ${response.statusText}`);
      }

      const result = await response.json();

      if (result.success) {
        this.state.isSuccess = true;
        this.emit("pledgeSubmitted", {
          pledgeId: result.pledgeId,
          total: this.state.total,
        });
      } else {
        this.state.errorMessage = result.error || "Failed to save pledge. Please try again.";
        this.emit("pledgeError", { error: this.state.errorMessage });
      }
    } catch (error: any) {
      this.state.errorMessage = error.message || "Failed to save pledge. Please try again.";
      this.emit("pledgeError", { error: this.state.errorMessage });
    } finally {
      this.state.isSubmitting = false;
      this.render();
    }
  }

  render() {
    if (this.state.isSuccess) {
      this.root.innerHTML = this.renderSuccess();
    } else {
      this.root.innerHTML = this.renderForm();
    }
    this.attachEventListeners();
  }

  private renderSuccess(): string {
    const totalFormatted = parseFloat(this.state.total).toLocaleString("en-US", {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    });

    return `
      <div class="nw-pledge">
        <div class="pledge-header success-header">
          <div class="success-title">Thank You!</div>
          <p class="success-message">
            We are grateful for your commitment to God's work.
          </p>
        </div>
        <div class="pledge-success-content">
          <div class="total-commitment">
            <div class="commitment-label">Total Commitment</div>
            <div class="commitment-amount">$${totalFormatted}</div>
          </div>
          <div class="success-text">
            <p>Your commitment has been recorded successfully.</p>
          </div>
        </div>
      </div>
    `;
  }

  private renderForm(): string {
    const { errors, isSubmitting, errorMessage } = this.state;
    
    return `
      <form class="nw-pledge" novalidate>
        <div class="pledge-header">
          <div class="pledge-title">MY COMMITMENT</div>
          
          <div class="form-row">
            <div class="form-field">
              <label for="firstName">FIRST NAME</label>
              <input type="text" id="firstName" name="firstName" required 
                class="${errors.firstName ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
            </div>
            <div class="form-field">
              <label for="lastName">LAST NAME</label>
              <input type="text" id="lastName" name="lastName" required 
                class="${errors.lastName ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
            </div>
          </div>

          <div class="form-field">
            <label for="email">EMAIL</label>
            <input type="email" id="email" name="email" required 
              class="${errors.email ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
          </div>

          <div class="form-field">
            <label for="phone">PHONE</label>
            <input type="tel" id="phone" name="phone" required placeholder="555-555-5555"
              class="${errors.phone ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
          </div>

          <div class="form-field">
            <label for="address">ADDRESS</label>
            <input type="text" id="address" name="address" required 
              class="${errors.address ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
          </div>

          <div class="form-row form-row-3">
            <div class="form-field">
              <label for="city">CITY</label>
              <input type="text" id="city" name="city" required 
                class="${errors.city ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
            </div>
            <div class="form-field">
              <label for="state">STATE</label>
              <select id="state" name="state" ${isSubmitting ? 'disabled' : ''}>
                ${this.renderStateOptions()}
              </select>
            </div>
            <div class="form-field">
              <label for="zipcode">ZIPCODE</label>
              <input type="text" id="zipcode" name="zipcode" required maxlength="5" pattern="[0-9]{5}" placeholder="12345"
                class="${errors.zipcode ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
            </div>
          </div>
        </div>

        <div class="pledge-body">
          <div class="commitment-header">WITH DEPENDENCE ON GOD I/WE COMMIT:</div>

          <div class="gift-section">
            <div class="gift-row">
              <div class="gift-label">COURAGEOUS GIFT</div>
              <div class="gift-input-wrapper">
                <span class="currency-symbol">$</span>
                <input type="text" name="courageous_gift" placeholder="0.00" 
                  class="${errors.courageous_gift ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
              </div>
            </div>
            <div class="gift-description">
              Given on or before Commitment Sunday on Nov. 9. If using a check, make it payable to "Northwoods" with a memo of "Now is the Time".
            </div>
          </div>

          <div class="gift-section">
            <div class="gift-row">
              <div class="gift-label">CONSISTENT GIFT</div>
              <div class="gift-input-wrapper">
                <span class="currency-symbol">$</span>
                <input type="text" name="consistent_gift" placeholder="0.00" 
                  class="${errors.consistent_gift ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
              </div>
            </div>
            <div class="gift-description">
              In addition to your Courageous Gift, the total over the next 25 months from Dec. 1, 2025 to Dec. 31, 2027.
            </div>
          </div>

          <div class="gift-section">
            <div class="gift-row">
              <div class="gift-label">CREATIVE GIFT</div>
              <div class="gift-input-wrapper">
                <span class="currency-symbol">$</span>
                <input type="text" name="creative_gift" placeholder="0.00" 
                  class="${errors.creative_gift ? 'error' : ''}" ${isSubmitting ? 'disabled' : ''}>
              </div>
            </div>
            <div class="gift-description">
              Provide an estimated value.
            </div>
            ${this.state.creativeGift ? `
              <div class="notes-section">
                <label for="notes">Describe the creative gift(s) of stocks, bonds, or other assets.</label>
                <textarea id="notes" name="notes" rows="3" ${isSubmitting ? 'disabled' : ''}></textarea>
              </div>
            ` : ''}
          </div>

          <div class="total-section">
            <div class="total-label">TOTAL GIFT</div>
            <div class="total-input-wrapper">
              <span class="currency-symbol">$</span>
              <input type="text" name="total_gift" value="${this.state.total}" readonly class="total-amount">
            </div>
          </div>
        </div>

        <div class="pledge-footer">
          ${errorMessage ? `
            <div class="error-message">${errorMessage}</div>
          ` : ''}
          <button type="submit" ${isSubmitting ? 'disabled' : ''}>
            ${isSubmitting ? 'Saving...' : 'Make My Pledge'}
          </button>
        </div>
      </form>
    `;
  }

  private renderStateOptions(): string {
    const states = [
      "AL", "AK", "AZ", "AR", "CA", "CO", "CT", "DE", "FL", "GA",
      "HI", "ID", "IL", "IN", "IA", "KS", "KY", "LA", "ME", "MD",
      "MA", "MI", "MN", "MS", "MO", "MT", "NE", "NV", "NH", "NJ",
      "NM", "NY", "NC", "ND", "OH", "OK", "OR", "PA", "RI", "SC",
      "SD", "TN", "TX", "UT", "VT", "VA", "WA", "WV", "WI", "WY"
    ];
    return states.map(state => 
      `<option value="${state}" ${state === "IL" ? "selected" : ""}>${state}</option>`
    ).join("");
  }

  private getStyles(): string {
    return `
      :host {
        all: initial;
        display: block;
        font-family: ui-sans-serif, system-ui, -apple-system, sans-serif;
      }

      .nw-pledge {
        max-width: 600px;
        margin: 0 auto;
        background: white;
        border-radius: 16px;
        overflow: hidden;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
      }

      .pledge-header {
        background: #002855;
        color: white;
        padding: 40px 32px;
      }

      .success-header {
        text-align: center;
      }

      .pledge-title, .success-title {
        font-size: 28px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 32px;
      }

      .success-title {
        font-size: 36px;
        margin-bottom: 24px;
      }

      .success-message {
        font-size: 18px;
        line-height: 1.6;
        margin: 0;
      }

      .pledge-success-content {
        padding: 40px 32px;
        text-align: center;
      }

      .total-commitment {
        margin-bottom: 24px;
      }

      .commitment-label {
        font-size: 12px;
        color: #6b7280;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        margin-bottom: 8px;
      }

      .commitment-amount {
        font-size: 48px;
        font-weight: bold;
        color: #002855;
      }

      .success-text {
        color: #374151;
        line-height: 1.6;
      }

      .form-field {
        margin-bottom: 16px;
      }

      .form-row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 12px;
        margin-bottom: 16px;
      }

      .form-row-3 {
        grid-template-columns: 2fr 1fr 1fr;
      }

      label {
        display: block;
        font-size: 12px;
        color: #d1d5db;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        margin-bottom: 6px;
      }

      input, select, textarea {
        width: 100%;
        padding: 12px 16px;
        border: 1px solid #d1d5db;
        border-radius: 8px;
        font-size: 14px;
        color: #1f2937;
        background: white;
        box-sizing: border-box;
      }

      input:focus, select:focus, textarea:focus {
        outline: none;
        background: #f9fafb;
        border-color: #004C97;
      }

      input.error, select.error, textarea.error {
        border: 2px solid #FF6D6A;
      }

      input:disabled, select:disabled, textarea:disabled, button:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }

      .pledge-body {
        padding: 32px;
      }

      .commitment-header {
        background: #002855;
        color: white;
        padding: 12px 20px;
        margin: -32px -32px 32px -32px;
        font-size: 18px;
        font-weight: bold;
        text-align: center;
      }

      .gift-section {
        margin-bottom: 32px;
      }

      .gift-row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 16px;
        align-items: center;
        margin-bottom: 12px;
      }

      .gift-label {
        font-size: 16px;
        font-weight: bold;
        color: #002855;
      }

      .gift-input-wrapper {
        display: flex;
        align-items: center;
        gap: 8px;
      }

      .currency-symbol {
        color: #6b7280;
        font-size: 16px;
      }

      .gift-input-wrapper input {
        flex: 1;
      }

      .gift-description {
        font-size: 13px;
        color: #374151;
        font-style: italic;
        line-height: 1.5;
        padding-left: calc(50% + 8px);
      }

      .notes-section {
        margin-top: 16px;
      }

      .notes-section label {
        color: #6b7280;
        font-size: 12px;
        text-transform: none;
        margin-bottom: 8px;
      }

      .total-section {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 16px;
        align-items: center;
        margin-top: 32px;
        padding-top: 32px;
        border-top: 2px solid #e5e7eb;
      }

      .total-label {
        font-size: 18px;
        font-weight: bold;
        color: #002855;
      }

      .total-input-wrapper {
        display: flex;
        align-items: center;
        gap: 8px;
      }

      .total-amount {
        font-size: 20px;
        font-weight: bold;
        color: #002855;
        border: none !important;
        background: transparent !important;
        padding-left: 0 !important;
      }

      .pledge-footer {
        background: #002855;
        padding: 32px;
        text-align: center;
      }

      .error-message {
        background: #fee;
        color: #c00;
        padding: 12px 16px;
        border-radius: 8px;
        margin-bottom: 16px;
        text-align: center;
      }

      button[type="submit"] {
        padding: 12px 32px;
        background: white;
        color: #002855;
        font-weight: bold;
        font-size: 16px;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        transition: background 0.2s;
      }

      button[type="submit"]:hover:not(:disabled) {
        background: #f3f4f6;
      }

      @media (max-width: 640px) {
        .form-row, .gift-row, .total-section {
          grid-template-columns: 1fr;
        }

        .form-row-3 {
          grid-template-columns: 1fr;
        }

        .gift-description {
          padding-left: 24px;
        }

        .pledge-header, .pledge-body, .pledge-footer {
          padding: 24px;
        }

        .commitment-header {
          margin: -24px -24px 24px -24px;
        }
      }
    `;
  }
}

customElements.define("nw-pledge", PledgeWidget);
